.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.project-content {
  display: flex;
  flex-direction: column;
  gap: 30px;
  margin-top: 20px;
}

.project-image-container {
  text-align: center;
}

.project-image {
  max-width: 100%;
  height: auto;
  border-radius: 10px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.1);
}

.project-image-caption {
  margin-top: 10px;
  font-size: 1rem;
  color: #666;
}

.project-text {
  font-size: 1.1rem;
  color: #333;
  line-height: 1.6;
}

.project-subtitles {
  font-size: 1.5rem;
  font-weight: 600;
  color: #00509e;
  margin: 20px 0 10px;
  line-height: 1.4;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  border-bottom: 2px solid #00509e;
  padding-bottom: 5px;
}
